

.btn {
	padding: .6rem .8rem;
	font-size: 1.2rem;
	text-align: center;
	border: 0;
	outline: none;
	border-radius: 10px;
	width: 120px;
	margin-left: 10px;
	box-shadow: 1px -3px 52px -5px rgba(0, 0, 0, 0.77);
	cursor: pointer;
}

.btn.add {
	background-color: var(--text-background);
	color: var(--tint);
	font-size: 18px;
	font-weight: 700;
	border-radius: 0;
}

.btn.remove {
	background-color: var(--text-background);
	color: var(--accent);
	font-size: 18px;
	font-weight: 700;
	border-radius: 0;
}

.btn.checkout {
	background-color: rgb(38, 121, 38);
	width: 150px;
	color: #fff;
	font-size: 14px;
}

.btn:active {
	transform: scale(0.98);
}

